@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.col{
  color:#4462c9;
}

.font{
  font-family: 'Poppins';
}

.wrapper {
  padding: 5px 20px;
  text-align: center;
  margin: 0;
  font-family: 'Poppins';
}

.wrapper fieldset {
  margin: 20px 0;
}


.wrapperSubmit {
  padding: 5px 20px;
  text-align: left;
  background: #4462c9;
  margin: 0;
  font-family: 'Poppins';
  color: white;
}

.button {
  background-color: #4462c9;
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  font-family: 'Poppins';
  cursor: pointer;
}
