
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap");

.col{
  color:#4462c9;
}

.font{
  font-family: 'Poppins';
}

.wrapper {
  padding: 5px 20px;
  text-align: center;
  margin: 0;
  font-family: 'Poppins';
}

.wrapper fieldset {
  margin: 20px 0;
}


.wrapperSubmit {
  padding: 5px 20px;
  text-align: left;
  background: #4462c9;
  margin: 0;
  font-family: 'Poppins';
  color: white;
}

.button {
  background-color: #4462c9;
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  font-family: 'Poppins';
  cursor: pointer;
}